<template>
	<view>
		<uni-nav-bar style="margin-bottom: 20rpx;"  @clickLeft="back" left-icon="back"  title="考试详情">
			<view slot="right">
				<text @click="openpicker" class="iconfont icon-caidan1"></text>
				<text>{{parseInt(currentIndex) + 1}}/{{ti_num}}</text>
			</view>
		</uni-nav-bar>
		
		
		<view  v-if="tiList[currentIndex] != undefined" class="shuati-wrao" @touchmove="handletouchmove" @touchstart="handletouchstart" @touchend="handletouchend">
			<!-- 题标题及描述部分 -->
			<view class="ti-top-wrap">
				<view class="title">
					<text class="ti-type">{{ti_type[tiList[currentIndex].type]}}</text>
					<text>{{tiList[currentIndex].title}}</text>
					<text class="score">({{Math.ceil(tiList[currentIndex].score)}}分)</text>
				</view>
				<image style="width:200rpx; height:200rpx;" v-if="tiList[currentIndex].answer.t_img != ''" :src="tiList[currentIndex].answer.t_img" mode=""></image>
				<imt-audio class="imtaudio" v-if="tiList[currentIndex].answer.t_audio != ''" :src="tiList[currentIndex].answer.t_audio"></imt-audio>
				<video class="ti-video" v-if="tiList[currentIndex].answer.t_video != ''" :src="tiList[currentIndex].answer.t_video" :poster="tiList[currentIndex].answer.t_video_img" controls></video>
			</view>
			
			<!-- 选项区 类型 0判断1单选2定项多选3简答4填空5不定项多选 -->
			<!-- 如果是选择题则序号 A B C D 如果填空题则 1 2 3-->
			<view class="ti-info-wrap">
						
					<!-- 判断题 rs为0错误 1正确 -->
					<block v-if="tiList[currentIndex].type == 0">
						<view @click="checkPanduan('1')" class="ti-info-item tiinfo-item-panduan-right">
							<text :class="{ti_info_item_xuhao_check:(tiend[currentQid] == '1')}" class="ti-info-item-xuhao">A</text>
							<text class="">正确</text>
						</view>
						<view @click="checkPanduan('0')" class="ti-info-item tiinfo-item-panduan-wrong">
							<text :class="{ti_info_item_xuhao_check:(tiend[currentQid] == '0')}" class="ti-info-item-xuhao">B</text>
							<text class="">错误</text>
						</view>
					</block>
					
					
					<!-- 单选题 -->
					<block v-else-if="tiList[currentIndex].type == 1">
						<view @click="checkDanxuan(index)" v-for="(item, index) in tiList[currentIndex].answer.ans" :key="index" class="ti-info-item">
							<!-- 这里用v-else会报错 -->
							<text :class="{ti_info_item_xuhao_check:(tiend[currentQid] == index)}" class="ti-info-item-xuhao">{{ti_info_item_abc[index]}}</text>
							<text class="">{{item}}</text>
						
						</view>
					</block>
					
					<!-- 定项选择题 -->
					<block v-else-if="tiList[currentIndex].type == 2">
						<view @click="checkDingxiang(index)" v-for="(item, index) in tiList[currentIndex].answer.ans" :key="dingxiang.key[index]" class="ti-info-item">
							<text v-if="dingxiang.abc[currentQid][index] != undefined && dingxiang.abc[currentQid][index].is_checked == '1'" class="is_checked ti-info-item-xuhao">{{ti_info_item_abc[index]}}</text>
							<text v-else class="ti-info-item-xuhao">{{ti_info_item_abc[index]}}</text>
							
							<text class="">{{item}}</text>
						</view>
						
						<view class="ans-btn-wrap">
							<text @click="subDingXiang" :class="{is_start_check : (dingxiang.is_start_check == '1')}" class="ans-btn">确认答案</text>
						</view>
					</block>
					
					<!-- 简答题 -->
					<block v-else-if="tiList[currentIndex].type == 3">
						<textarea maxlength="-1" class="txt-area" @input="listenTxtArea" v-model="jianda.ans[currentIndex]" value="" placeholder="点此输入答案" placeholder-style="color:#C8C8C8;" />
						
						<view class="ans-btn-wrap ans-jianda-wrap">
							<text @click="subJianDa" :class="{is_start_check : (jianda.is_start_check == '1')}" class="ans-btn">确认答案</text>
						</view>
					</block>
					
					<!-- 填空题 -->
					<block v-else-if="tiList[currentIndex].type == 4">	
						<!-- 根据答案 控制input数量 -->
						<view v-for="(item,index) in tiList[currentIndex].answer.ans" :key="index" class="ti-info-item ti-info-item-tiankong">
							<text class="ti-info-item-xuhao ti-info-item-xuhao-num">{{parseInt(index)+1}}</text>
							<input class="tiankong-input" @input="listentiankong" v-model="tiankong.ans[currentIndex][index]" style="border: 2rpx solid #DDDDDD;" type="text" value="" />
						</view>
						
						<view class="ans-btn-wrap">
							<text @click="subTiankong" :class="{is_start_check : (tiankong.is_start_check == '1')}" class="ans-btn">确认答案</text>
						</view>
					</block>
					
					<!-- 不定项多选 -->
					<block v-else-if="tiList[currentIndex].type == 5">
						<view @click="checkBudingXiang(index)" v-for="(item, index) in tiList[currentIndex].answer.ans" :key="budingxiang.key[index]" class="ti-info-item">
							<text :class="{is_checked : (budingxiang.abc[currentQid][index] != undefined && budingxiang.abc[currentQid][index].is_checked == '1')}" class="ti-info-item-xuhao">{{ti_info_item_abc[index]}}</text>
							<text class="">{{item}}</text>
							
						</view>
						
						<view class="ans-btn-wrap">
							<text @click="subBudingXiang" :class="{is_start_check : (budingxiang.is_start_check == '1')}" class="ans-btn">确认答案</text>
						</view>
					</block>
						
			</view>
			
			<!-- 计时和交卷区域 -->
			<view class="jishi-jiaojuan">
				<view class="jishi">
					<text v-if="exp != '-1'">
						倒计时
					</text>
					&nbsp;{{timeTxt}}
				</view>
				<view class="jiaojuan-btn-wrap">
					<text @click="handpaper" class="jiaojuan-btn">交卷</text>
				</view>
			</view>
		</view>
			
		<!-- 遮罩区域 -->
		<!-- 遮罩上层 -->
		<view @click="closezhetop" v-if="is_zhe_top == '1'" class="zhetop">
		</view>
		
		
		 <view v-if="is_zhe_show == '1'" class="zhe">
			<view class="zhe-title">题目列表</view>
			<!-- 题号区域 -->
			<view class="tihao-wrap">
				<text @click="checkByTihao(index)" :class="{tihao_end:(tiend[parseInt(index)+1] != undefined)}" class="tihao-item" v-for="(item,index) in ti_num">{{parseInt(index)+1}}</text>
				
			</view>
		 </view>  
		
		<!-- 滑动提示遮罩区 -->
		<view v-if="openKaoshiInfoNum == '1'" class="zhehua">
			 <image @click="huaNotice" class="zhehua_img" src="../../static/images/hua_tishi.png" mode="aspectFit"></image>
		</view>
		
		
		
	</view>
</template>

<script>
	import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
	import imtAudio from '@/components/imt-audio/imt-audio.vue';
	const app = getApp();
	
	export default {
		components:{
			uniNavBar,
			imtAudio
		},
		data() {
			return {
				mask: false,
				isShowscore: '', //是否显示分值
				exp: 0, //时长（秒） 0表示已结束 -1无时间限制
				ti_num: 0,
				tiList: [], //题目列表
				currentIndex: '0', //当前页面索引
				currentQid: '1', //当前题号 (索引+1)
				// 0判断1单选2定项多选3简答4填空5不定项多选
				ti_type: ['判断', '单选', '定项', '简答', '填空', '不定项'],
				user_answer: {
					'dui_num' : 0,
					'cuo_num' : 0,
					'ti_data' : []
				},	
				ti_info_item_abc: ['A', 'B', 'C', 'D', 'E'], 
				testsid: '', //试题id
				tiend: [], //存储当前题的题目答题情况 {题号1: 选项X, 题号2: 选项X}
				// 定项选择题相关数据
				dingxiang: {
					'ans' : [], //存放每次点击答案时的选项id
					'abc':[], 
					'is_start_check': '', //是否已经开始选择
					'sub': [], //是否提交 题号:是否提交 {'1' : '1', '2': '1', ...}
					'key' : {'0':'0', '1':'1', '2':'2', '3':'3'},
				},
				// 填空题相关数据
				tiankong: {
					'ans': [], //存放用户input输入的答案
					'sub' : [], //是否提交 id: 是否提交 {'1' : '1', '2': '1', ...}
					'is_start_check': '', //是否已经开始输入
				},
				// 不定项选择题相关数据
				budingxiang: {
					'ans': [], //存放用户每次点击答案时的选项id
					'abc': [], //{'is_checked': '0'}, 是否已经选择
					'is_start_check': '', //是否已经开始选择 
					'sub': [], //是否提交 题号:是否提交 {'1' : '1', '2': '1', ...}
					'key' : {'0':'0', '1':'1', '2':'2', '3':'3', '4':'4'}, //占位key, 强制组件重新渲染
				},
				// 简答题相关数据
				jianda: {
					'ans': [], //用户填写的简答题答案 索引 => 答案
					'is_start_check': '', //是否已经开始输入
				},
				is_zhe_show: '0', //是否显示遮罩层
				is_zhe_top: '0', //是否显示遮罩层上半边
				strWindow: false,
				userinfo: '',
				timeTxt: '', //倒计时文本
				openKaoshiInfoNum: 0, //打开本页的次数
				// 滑动属性 
				flag: 0,//1向左滑动,2向右滑动,3向上滑动 4向下滑动
				text: '',//向哪里滑动
				lastX: 0,
				lastY: 0,
				index:0
			}
		},
		onReady() {
			this.openKaoshiInfoNum = ++(app.globalData.open_kaoshi_info_num);
		},
		onLoad(option) {
			
			this.testsid = option.testsid;
			let type = option.type;
			this.currentQid = parseInt(this.currentIndex) + 1;
			let userInfo = uni.getStorageSync('userinfo');
			if(!userInfo) {
				uni.navigateTo({
					url: '../login/login',
				});
			}
			this.userinfo = userInfo;
			
			// 处理不定项和定项题号数组
			if(this.dingxiang.abc[this.currentQid] == undefined) {
				this.dingxiang.abc[this.currentQid] = [];
				this.$set(this.dingxiang.abc, this.currentQid, []); //赋值定项题号数组
			}
			if(this.budingxiang.abc[this.currentQid] == undefined) {
				this.budingxiang.abc[this.currentQid] = [];
				this.$set(this.budingxiang.abc, this.currentQid, []); //赋值不定项题号数组
			}
			
			if(type != undefined) {
				if(type == '0') {
					// 新做/重做
					this.getTopic(this.testsid, '0'); //获取题目
				} 
				// 继续答题
				else if(type == '1') {
					this.getTopic(this.testsid, '1');
				}
			}
			
		},
		methods: {
			
			// 滑动提示
			huaNotice() {
				app.globalData.open_kaoshi_info_num++;
				this.openKaoshiInfoNum++;
			},
			// 转换为分秒
			setMiddleSecond(value) {
			    let theTime = parseInt(value);// 秒
			    let middle= 0;// 分
			
			    if(theTime > 60) {
			        middle= parseInt(theTime/60);
			        theTime = parseInt(theTime%60);
			    }
				
				if(String(theTime).length < 2) {
					theTime = (Array(2).join(0) + theTime).slice(-2);
				}
			    let result = ""+theTime;
			    if(middle > 0) {
			        result = ""+middle+":"+result;
			    }
			    return result;
			},
			  //倒计时
			resetCode(){
				
				// 获取剩余时间
				if(this.exp < 0) {
					return;
				} 
				let that = this;
				let timer = null;
				timer = setInterval(function(){
					if(that.exp > 0 ){
						that.exp--;
						that.timeTxt = that.setMiddleSecond(that.exp);
					}else{
						that.timeTxt = 0;
						clearInterval(timer);
					}
				},1000);
				
			},
			// 根据题号切换题目
			checkByTihao(index) {
				this.currentIndex = index;
				this.currentQid = parseInt(index) + 1;
				console.log(this.tiend);
				
			},
			// 关闭遮罩上层
			closezhetop() {
				this.is_zhe_top = '0';
				this.is_zhe_show = '0';
				this.strWindow = !this.strWindow; 
			},
			// 弹出遮罩
			openpicker() {
				this.is_zhe_show = '1';
				this.is_zhe_top = '1';
				this.strWindow = !this.strWindow;
			},
			// 交卷
			handpaper() {
				let that = this;
				let showContent = '确定要交卷吗?';
				if(parseInt(that.exp) > 0) {
					showContent = '考试还未结束, 确定要提前交卷吗?';
				}
				// 如果不到时长则 考试还未结束, 确定要提前交卷吗?
				uni.showModal({
					title: '提示',
					content: showContent,
					showCancel: true,
					cancelColor: "#C7C7C7",
					confirmColor: "#38DAA6",
					success: res => {
						 if (res.confirm) {
							console.log('用户点击确定');
							
							let gData = app.globalData;
							uni.request({
								url: gData.site_url + 'Tests.EndTests',
								method: 'GET',
								data: {
									// uid token testsid
									'uid' : that.userinfo.id,
									'token' : that.userinfo.token,
									'testsid' : that.testsid
								},
								success: res => {
									console.log(res);
									console.log(that.testsid);
									// 跳转成绩页
									uni.navigateTo({
										url: '../kaoshi_result/kaoshi_result?testsid=' + that.testsid,
									});
								},
								fail: () => {
									uni.showToast({
										icon: 'none',
										title: '网络错误'
									});
									return;
								},
							});
							
							
						 } else if (res.cancel) {
							console.log('用户点击取消');
						 }
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
				});
				
			},
			// 监听多文本框
			listenTxtArea(e) {
				if(e.detail.value != '') {
					this.jianda.is_start_check = '1';
				} else {
					this.jianda.is_start_check = '';
				}
			},
			// 提交简答 
			subJianDa(){
				let jianda_ans = this.jianda.ans[this.currentIndex];
				if(jianda_ans == '') {
					uni.showToast({
						icon: 'none',
						title: '答案不能为空'
					});
					return;
				}
				
				let qid = this.currentQid; //题号
				
				let usrAns = {'qid' : qid, 'rs' : jianda_ans};
				this.setAnswer(usrAns);
				this.jianda.is_start_check = '';
				
			},
			// 监听填空 下方按钮变色
			listentiankong(e) {
				if(e.detail.value != '') {
					this.tiankong.is_start_check = '1';
				} else {
					this.tiankong.is_start_check = '';
				}
			},
			// 提交填空题
			subTiankong() {
				let tiankong_ans = this.tiankong.ans;
				console.log(tiankong_ans);
				let kong_num = 0;
				for (let idx in tiankong_ans[this.currentIndex]) {
					if(tiankong_ans[this.currentIndex][idx] == '') {
						kong_num++;
					}
				}
				if(kong_num == tiankong_ans[this.currentIndex].length) {
					uni.showToast({
						icon: 'none',
						title: '答案不能为空'
					});
					return;
				}
				
				let qid = this.currentQid; //题号
				let rs = tiankong_ans[this.currentIndex].join(',');
				let usrAns = {'qid' : qid, 'rs' : rs};
				this.setAnswer(usrAns, '4');
				this.tiankong.is_start_check = '';
			},
			// 处理不定项选择题 us用户所选答案索引
			checkBudingXiang(us) {
				// 强制重新渲染页面数据
				for (let idx in this.budingxiang.key) {
						this.budingxiang.key[idx]++;
				}
				us = us.toString();
				let	usr_ans = this.budingxiang.ans;
				let qid = this.currentQid; //题号
				
				if(usr_ans.indexOf(us) != '-1') {
					this.budingxiang.ans.splice(usr_ans.indexOf(us), 1);
					this.budingxiang.abc[qid][us] = {'is_checked' : ''};
					this.$set(this.budingxiang.abc[qid], us, {'is_checked' : ''});
				} else {
					this.budingxiang.ans.push(us);
					this.budingxiang.abc[qid][us] = {'is_checked' : '1'};
					this.$set(this.budingxiang.abc[qid], us, {'is_checked' : '1'});
				}
				
				this.budingxiang.is_start_check = '1';
				if(this.budingxiang.ans.length == 0) {
					this.budingxiang.is_start_check = '';
				}
				
			},
			//提交不定项答案
			subBudingXiang() {
				if(this.budingxiang.ans.length == 0) {
					uni.showToast({
						icon: 'none',
						title: '请选择答案'
					});
					return;
				}
				// 去重
				this.budingxiang.ans = this.uniqueArr(this.budingxiang.ans);
				let qid = this.currentQid;
				let rs = this.budingxiang.ans.join(',');
				let usrAns = {'qid' : qid, 'rs' : rs};
				this.setAnswer(usrAns, '5');
				this.budingxiang.is_start_check = '';
			},
			/* 处理定项选择题(必须全部答案都选中才算做对)
			 * @param {Object} us 用户所选索引
			 */
			checkDingxiang(us){
				// 强制重新渲染页面数据
				for (let idx in this.dingxiang.key) {
					this.dingxiang.key[idx]++;
				}
				us = us.toString();
				let	usr_ans = this.dingxiang.ans;
				let qid = this.currentQid; //题号
				
				if(usr_ans.indexOf(us) != '-1') {
					this.dingxiang.ans.splice(usr_ans.indexOf(us), 1); //删除
					this.dingxiang.abc[qid][us] = {'is_checked' : ''};
					this.$set(this.dingxiang.abc[qid], us, {'is_checked' : ''}); //赋值题号对应的选项为选中
				} else {
					this.dingxiang.ans.push(us);
					this.dingxiang.abc[qid][us] = {'is_checked' : '1'};
					this.$set(this.dingxiang.abc[qid], us, {'is_checked' : '1'});
				}
				
				this.dingxiang.is_start_check = '1';
				if(this.dingxiang.ans.length == 0)  {
					this.dingxiang.is_start_check = '';
				}
			},
			// 提交定项答案
			subDingXiang() {
				if(this.dingxiang.ans.length == 0) {
					uni.showToast({
						icon: 'none',
						title: '请选择答案'
					});
					return;
				}
				// 去重
				this.dingxiang.ans = this.uniqueArr(this.dingxiang.ans);
				let qid = this.currentQid;
				let rs = this.dingxiang.ans.join(',');
				let usrAns = {'qid' : qid, 'rs' : rs};
				this.setAnswer(usrAns);
				this.dingxiang.is_start_check = '';
			},
			/* 处理判断题
			 * @param {Object} us 用户点击的答案 1正确 0错误
			 */
			checkPanduan(us) {
				let qid = this.currentQid;
				// 调用提交答案接口
				let usrAns = {'qid': qid, 'rs': us};
				this.setAnswer(usrAns);
			},
			/* 处理单选题
			 * @param {string} us 用户所选的答案索引
			 */
			checkDanxuan(us) {
				let qid = parseInt(this.currentIndex) + 1; //当前索引加1是题号
				// 调用提交答案接口
				let usrAns = {'qid': qid, 'rs': us}; 
				this.setAnswer(usrAns);
			},
			/**qid 题号 rs用户提交答案索引 img
			 * @param {Object} usrAns
			 * @param {type} string 题目类型  
			 */
			setAnswer(usrAns, type= ''){
				let gData = app.globalData;
				
				let that = this;
				uni.request({
					url: gData.site_url + 'Tests.SetAnswer',
					method: 'GET',
					data: {
						// uid token testsid result
						'uid' : that.userinfo.id,
						'token' : that.userinfo.token,
						'testsid' : that.testsid,
						'result' : usrAns
					},
					success: res => {
						console.log(res);
						if(res.data.data.code == '0') {
							// 存储全局变量 提交此题答案成功 输入框置空 选项变色
							// this.tiankong.ans.length = 0;
							// 题id1 : {'题号1', '题号2', ....}
							let testsid = that.testsid;
							// {this.testsid : {'qid'}};
							let tiend = app.globalData.kaoshi_tiend[testsid];
							
							if(tiend != undefined) {
								app.globalData.kaoshi_tiend[testsid][usrAns.qid] = usrAns.rs;
							} else {
								app.globalData.kaoshi_tiend[testsid] = {};
								app.globalData.kaoshi_tiend[testsid][usrAns.qid] = usrAns.rs;
							}
							
							this.tiend[usrAns.qid] = usrAns.rs;
							this.$set(this.tiend, usrAns.qid, usrAns.rs);
						} 
						else if(res.data.data.code == '700') {
							uni.navigateTo({
								url: '../login/login',
							});
						}
						
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
				});
			},
			// 获取试题题目
			getTopic(testsid, type='0') {
				let gData = app.globalData;
				let that = this;
				uni.request({
					url: gData.site_url + 'Tests.GetTopic',
					method: 'GET',
					data: {
						// uid token testsid type 0新做/重做 1继续做 2只看错误 3看全部
						'uid' : that.userinfo.id,
						'token' : that.userinfo.token,
						'testsid' : testsid,
						'type' : type
					},
					success: res => {
						console.log(res);
						if(res.data.data.code != '0') {
							return;
						}
						this.isShowscore = res.data.data.info[0].showscore;
						this.exp = parseInt(res.data.data.info[0].exp);
						this.tiList = res.data.data.info[0].list;
						this.ti_num = this.tiList.length;
						
						// 填充填空题答案数组
						for (let i = 0; i < this.ti_num; i++) {
							this.tiankong.ans[i] = [];
						}
						
						if(this.exp == -1) {
							this.timeTxt = '时长无限制';
						}
						if(this.exp > 0) {
							// 倒计时
							this.resetCode();
						}
						
						if(type == '1') {
							//继续答题
							let tiList = this.tiList;
							for (let idx in tiList) {
								// 是否已经答此题
								if(tiList[idx].isans != '1') {
									continue;
								}
								let currentQid = parseInt(idx)+1;
								//判断题/单选题
								if(tiList[idx].type == '0' || tiList[idx].type == '1') {
									// 这里得用题目索引加1不能用currentQid
									this.tiend[currentQid] = tiList[idx].rs_user.rs;
								} 
								// 定项选择题 (定项和不定项不能用tiend存, 小程序会报indexOf()错误)
								else if(tiList[idx].type == '2') {
									let user_rs_arr = (tiList[idx].rs_user.rs).split(',');
									this.dingxiang.abc[currentQid] = [];	
									for (var i = 0; i < tiList[idx].answer.nums; i++) {
										let i_str = i.toString();
										if(user_rs_arr.indexOf(i_str) != '-1') {
											this.dingxiang.abc[currentQid][i_str] = {'is_checked' : '1'};
											this.dingxiang.ans.push(i_str);
										} else {
											this.dingxiang.abc[currentQid][i_str] = {'is_checked' : ''};	
										}
									}
									
								}
								// 简答
								else if(tiList[idx].type == '3') {
									this.jianda.ans[idx] = tiList[idx].rs_user.rs;
								}
								// 填空
								else if(tiList[idx].type == '4') {
									let user_rs_arr = (tiList[idx].rs_user.rs).split(',');
									this.tiankong.ans[idx] = new Array();
									for (var i = 0; i < user_rs_arr.length; i++) {
										this.tiankong.ans[idx][i] = user_rs_arr[i]; 
									}
								}
								//不定项选择题
								else if(tiList[idx].type == '5') {
									let user_rs_arr = (tiList[idx].rs_user.rs).split(',');
									this.budingxiang.abc[currentQid] = [];
									for (var i = 0; i < tiList[idx].answer.nums; i++) {
										let i_str = i.toString();
										if(user_rs_arr.indexOf(i_str) != '-1') {
											this.budingxiang.abc[currentQid][i_str] = {'is_checked' : '1'};
											this.budingxiang.ans.push(i_str);
										} else {
											this.budingxiang.abc[currentQid][i_str] = {'is_checked' : ''};
										}
									}
									
								}
								
							}
							console.log(this.tiankong.ans);
						}
						
						
					},
					fail: () => {
						uni.showToast({
							icon: 'none',
							title: '网络错误'
						});
					},
				});
			},
			// 数组去重
			uniqueArr(arr) {
				return Array.from(new Set(arr))
		    },
			back() {
				
				uni.navigateBack({
					delta: 1
				});
			},
			handletouchmove: function(event) {
				
				if (this.flag !== 0) {
					return;
				}
				let currentX = event.changedTouches[0].pageX;
				let currentY = event.changedTouches[0].pageY;
				let tx = currentX - this.lastX;
				let ty = currentY - this.lastY;
				let text = '';
				this.mindex = -1;
				//左右方向滑动
				if (Math.abs(tx) > Math.abs(ty)) {
					if (tx < 0) {
						text = '向左滑动';
						this.flag = 1;
					   //切换下一题
					   if(this.currentIndex == (this.tiList.length-1)) {
							return;
					   }
					  this.currentIndex++; 
					  this.currentQid++;
					  
					} else if (tx > 0) {
						text = '向右滑动';
						this.flag = 2;
						// 切换上一题
						if(this.currentIndex == '0') {
							return;
						}
						this.currentIndex--; 
						this.currentQid--;
					}
					
					let qid = this.currentQid;
					if(this.dingxiang.abc[qid] == undefined) {
						
						this.dingxiang.abc[qid] = [];
						this.$set(this.dingxiang.abc, qid, []); //赋值定项题号数组
					}
					if(this.budingxiang.abc[qid] == undefined) {
						this.budingxiang.abc[qid] = [];
						this.$set(this.budingxiang.abc, qid, []); //赋值不定项题号数组
					}
					
				}
				//上下方向滑动
				else {
					if (ty < 0) {
						text = '向上滑动';
						this.flag = 3;
					//  this.getList();  //调用列表的方法
					} else if (ty > 0) {
						text = '向下滑动';
						this.flag = 4;
					}
				}
				
				//将当前坐标进行保存以进行下一次计算
				this.lastX = currentX;
				 this.lastY = currentY;
				this.text = text;
			},
			handletouchstart: function(event) {
				// console.log(event)
				this.lastX = event.changedTouches[0].pageX;
				this.lastY = event.changedTouches[0].pageY;
			},
			handletouchend: function(event) {
				this.flag = 0;
				this.text = '没有滑动';
			},
			
		}
	}
</script>

